@import url(//fonts.googleapis.com/css?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css?family=Manrope&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media all and (min-width: 480px) {
    .PageFrame
    {
        margin: 100px;
        background-color: red;


    }
    .Login {
        padding: 60px 0;
        background-color:white;


    }

    .Login form {

        margin: 0 auto;
        max-width: 320px;
        background-color:white;
        position: absolute;
        right: 15%;
        bottom: 40%;





    }

    .Form
    {

    }

    .LoginText
    {
        padding-bottom: 20px;

    }

    .LogoImage
    {
        display: block;
        position: relative;
        width: 550px;
        height: 300px;
        top: 310px;
        right: 520px;
    }



    .EmailBox
    {

        border-radius: 100px;
        background-color:white;
        border: none;
        width: 230px;
        height: 37px;
        text-align: center;
        font-family: 'Nunito', sans-serif;


    }
    .PasswordBox {
        margin-top: 10px;
        border-radius: 100px;
        background-color: #e6e6e6;
        border: none;
        width: 230px;
        height: 37px;
        text-align: center;
    }
    .LoginBtn
    {
        margin-top: 40px;
        border: none;
        width: 230px;
        height: 37px;
        border-radius: 100px;
        background-color:#23c7ac;
        color:white;
        font-size: 20px;

    }

    body
    {
        background-color: white;
        text-align: center;
    }

    input:focus {outline: none; }
}

.PanelPos
{
display: flex;

}
.Panel
{
    height: 100%;
    width: 160px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #2b313d;
    overflow-x: hidden;
    padding-top: 20px;
}

li a
{
color: white;
    text-decoration: none;
    font-family: 'Manrope', sans-serif;
    font-size: 22px;
}
.b
{
    background-color: #2b313d;
    width: 100%;
    padding-bottom: 7px;
    padding-top: 7px;
    transition: all 0.3s ease 0s;
}


.b:hover
{
    background-color: #1fb299 ;

}

.maps-icon{
    height: 25px;
    width: 25px;
    top: 10px;
}

.vol-icon{
    height: 25px;
    width: 20px;
}

.logo{
    height: 110px;
    width: 110px;
}

.ambrosia-title{
    color:white;
    font-size: 25px;
}

.signOutBorder{
    position: absolute;
    padding-bottom: 7px;
    left:0;
    bottom:20px;
    right:0;

}



.page-switcher
{
    position: fixed;
    bottom: 0;
    right: 0;
    left:0;
}

.table
{
    position:fixed;
    bottom: 0;
    right: 0;
    left:150px;
    top:0;

}
.button
{
    background-color: #2b313d;
    width: 50%;
    padding-bottom: 7px;
    padding-top: 7px;
    transition: all 0.3s ease 0s;
    color: white;
}


.button:hover
{
    background-color: #1fb299 ;

}

.search {
    position: absolute;
    top: 1rem;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 400px;
    z-index: 10;

}

.search input {
    padding: 0.5rem;
    font-size: 1.5rem;
    width: 100%;
}


.remove-marker
{

    width: 107px;
    height: 25px;
    font-family: 'Manrope', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 700;
    color: white;
    background-color: #2b5876;
    border: none;
    border-radius: 45px;
    box-shadow: 8px 8px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
    display: block;
    margin-bottom: 5px;
}
.remove-marker:hover{
    background-color:#cb2d40 ;

}
.removeBlue-marker:hover{
    background-color:#cb2d40 ;

}
.removeGrey-marker:hover{
    background-color:#cb2d40 ;

}
.removeYellow-marker:hover{
    background-color:#cb2d40 ;

}
.transform-marker:hover{
    background-color:#cb2d40 ;

}
.forSure-marker:hover{
    background-color:#cb2d40 ;
}
